.hotel-search {
  padding: 39px 18px 49px;
  text-align: center;
}

.hotel-search__content {
  text-align: center;
  padding: 0 11px 32px;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    padding: 0 166px 44px;
  }

  @media (min-width: $desktop-width) {
    padding: 0 382px 44px;
  }
}

.hotel-search__title {
  font-size: 21px;
  line-height: 26px;
  font-weight: 700;
  text-transform: uppercase;
  margin: 0;
  padding-bottom: 24px;
}

.hotel-search__text {
  margin: 0;
  color: #333333;
}

.hotel-search__button {
  width: 284px;

  @media (min-width: $tablet-width) {
    width: 458px;
  }
}
